export default {
  layout: 'login-register',
  middleware: ['guest'],
  data() {
    return {
      loading: false,
      form: {
        username: {
          type: 'text',
          val: '',
          placeholder: 'Username...',
          icon: 'fas fa-user',
          id: 'registerUsername',
        },
        name: {
          type: 'text',
          val: '',
          placeholder: 'Name...',
          icon: 'fas fa-address-card',
          id: 'registerName',
        },
        email: {
          type: 'text',
          val: '',
          placeholder: 'Email...',
          icon: 'fas fa-envelope',
          id: 'registerEmail',
        },
        password: {
          type: 'password',
          val: '',
          placeholder: 'Password...',
          icon: 'fas fa-user-lock',
          id: 'registerPassword',
        },
        password_confirmation: {
          type: 'password',
          val: '',
          placeholder: 'Password Confirmation...',
          icon: 'fas fa-user-lock',
          id: 'registerPasswordConfirmation',
        },
      },
      errors: {},
      alert: {
        class: '',
        show: false,
        text: '',
      },
    }
  },
  methods: {
    async formSubmit() {
      this.errors = {}
      this.alert = {}
      this.loading = true
      try {
        await this.$axios.$post('/register', {
          username: this.form.username.val,
          name: this.form.name.val,
          password: this.form.password.val,
          password_confirmation: this.form.password_confirmation.val,
          email: this.form.email.val,
        })
        // this.clear()
        this.alert.class = 'alert-success'
        this.alert.show = true
        this.alert.text = `Success! Please verify your email <strong>${this.form.email.val}</strong>`
      } catch (e) {
        this.errors = e.response.data.errors
      } finally {
        this.loading = false
      }
    },
    clear() {
      Object.assign(this.$data, this.$options.data())
    },
  },
}
